import React, { useEffect, useState } from "react";
import Logotyp from "../../data/img/logo.png";

function Logo({ h }) {
    const [height, setHeight] = useState("40px");
    useEffect(() => {
        if (Boolean(h)) setHeight(h);
    }, []);
    return (
        <img
            style={{
                height: `${height}`,
            }}
            src={Logotyp}
            alt="Логотип"
        />
    );
}

export default Logo;
