import { Tab, Tabs, Toolbar } from "@mui/material";
import React, { useEffect, useState } from "react";

const Validation = ({
    product,
    setProduct,
    category,
    setCategory,
    FullCategory,
}) => {
    const [value, setValue] = useState(0);

    const Validation = () => {
        let result = [];
        if (category.length && category !== "Все") {
            for (let i of product) {
                if (category.includes(i.category)) {
                    result.push(i);
                }
            }
        } else result = product;
        setProduct(result);
    };

    const AddValue = () => {
        for (let i of FullCategory) {
            if (i.category === category) {
                setValue(i.Value);
            }
        }
    };

    useEffect(() => {
        Validation();
        AddValue();
    }, [category, FullCategory]);

    return (
        <Toolbar>
            <Tabs
                variant="fullWidth"
                sx={{ width: "100%", borderBottom: "1px solid #CCCCCC", m: 2 }}
                value={value}
            >
                <Tab
                    label={"Все"}
                    onClick={() => {
                        setValue(0);
                        setCategory("Все");
                    }}
                />
                {FullCategory.map((i) => (
                    <Tab
                        key={i.category}
                        label={i.category}
                        onClick={() => {
                            setValue(i.Value);
                            setCategory(i.category);
                        }}
                    />
                ))}
            </Tabs>
        </Toolbar>
    );
};

export default Validation;
