import { Box, Grid, Typography } from "@mui/material";
import React from "react";

function ProductMenu({ FullCategory, setCategory }) {
    return (
        <Grid container spacing={5} padding={2}>
            {FullCategory.map((i, index) => (
                <Grid
                    key={index}
                    item
                    xs={6}
                    sm={6}
                    lg={4}
                    sx={{
                        display: "flex",
                        justifyContent: "center",
                    }}
                >
                    <Box
                        sx={{
                            display: "flex",
                            justifyContent: "center",
                            alignItems: "center",
                            cursor: "pointer",
                            height: "400px",
                            width: "400px",
                            border: "2.5mm solid #781026",
                            borderRadius: "10px",
                            backgroundImage: `url(${i.img})`,
                            backgroundRepeat: "no-repeat",
                            backgroundSize: "cover",
                            backgroundPosition: "center",
                            transition: "300ms",
                            "&:hover": {
                                borderColor: "#FFBFA0",
                                transform: "scale(1.1)",
                            },
                        }}
                        onClick={() => setCategory(i.category)}
                    >
                        <Typography
                            sx={{
                                fontWeight: "600",
                            }}
                            color={"white"}
                            variant="h2"
                        >
                            {i.category}
                        </Typography>
                    </Box>
                </Grid>
            ))}
        </Grid>
    );
}

export default ProductMenu;
