import { Button, Typography } from "@mui/material";
import React from "react";

const styles = {
    root: {
        display: "flex",
        gap: "15px",
        alignItems: "center",
    },
};

export const Counter = ({ product, setNumber, list }) => {
    const NumberPlus = () => {
        let result = [];
        for (let i of list) {
            if (product.id === i.id) {
                i.Quantity++;
                result.push(i);
            } else {
                result.push(i);
            }
        }
        setNumber(result);
    };

    const NumberMinus = () => {
        let result = [];
        for (let i of list) {
            if (product.id === i.id) {
                i.Quantity--;
                result.push(i);
            } else {
                result.push(i);
            }
        }
        setNumber(result);
    };

    return (
        <div style={styles.root}>
            <Button
                disabled={!product.Quantity}
                onClick={NumberMinus}
                children={<Typography variant="h5" children={"-"} />}
            />

            <Typography variant="h6" children={product.Quantity} />

            <Button
                onClick={NumberPlus}
                children={<Typography variant="h5" children={"+"} />}
            />
        </div>
    );
};
