import { Box, Container, Modal, Typography } from "@mui/material";
import React, { useEffect, useState } from "react";
import { BasketHeader } from "./BasketHeader";
import BasketListL from "./BasketList";
import { BasketPrice } from "./BasketPrice";

const styles = {
    root: {
        display: "flex",
        flexDirection: "column",

        position: "relative",
        height: { xs: "100%", sm: "90%", md: "90%" },
        width: { xs: "100%", sm: "90%", md: "60%" },
        bgcolor: "background.paper",
        borderRadius: 3,
        border: "2px solid #000",

        boxShadow: 24,
        top: "50%",
        left: "50%",
        transform: "translate(-50%, -50%)",
    },
};

function Basket({ open, onClose, BasketList, BasketAdd, setBasketList }) {
    const [Total, setTotal] = useState(0);

    const sumTotal = () => {
        let sum = 0;
        for (let i of BasketList) {
            sum += +i.prise * i.Quantity;
        }
        setTotal(sum);
    };

    useEffect(() => {
        sumTotal();
    }, [BasketList]);
    return (
        <Modal open={open} onClose={onClose}>
            <Box sx={styles.root}>
                <BasketHeader onClose={onClose} />
                <Container sx={{ overflow: "auto" }}>
                    <BasketListL
                        onClose={onClose}
                        setBasketList={setBasketList}
                        BasketAdd={BasketAdd}
                        BasketList={BasketList}
                    />

                    <BasketPrice
                        Total={Total}
                        BasketList={BasketList}
                        onClose={onClose}
                    />
                </Container>
            </Box>
        </Modal>
    );
}

export default Basket;
