import { Delete, MoreVert } from "@mui/icons-material";
import {
    Box,
    Divider,
    IconButton,
    ListItemIcon,
    Menu,
    MenuItem,
    Typography,
} from "@mui/material";
import React, { useState } from "react";
import { Link } from "react-router-dom";
import { Counter } from "../UI/Counter";

const styles = {
    Box: {
        height: "100px",
        margin: "24px 0",
        Body: {
            display: "flex",
            justifyContent: "space-between",
            Body: {
                display: "flex",
                gap: "20px",
                Img: {
                    borderRadius: "10px",
                    width: "90px",
                    height: "90px",
                },
            },
        },
        Title: {
            display: "flex",
            justifyContent: "flex-end",
            alignItems: "center",
            mt: "-35px",
            gap: "20px",
            mb: "15px",
        },
        Divider: { bgcolor: "black" },
    },
};

function BasketItem({
    product,
    BasketAdd,
    setBasketList,
    BasketList,
    onClose,
}) {
    const [OpenMenu, setOpenMenu] = useState(null);

    return (
        <Box sx={styles.Box}>
            <Box sx={styles.Box.Body}>
                <Box sx={styles.Box.Body.Body}>
                    <Link onClick={onClose} to={`/productions/${product.id}`}>
                        <img
                            style={styles.Box.Body.Body.Img}
                            alt={product.title}
                            src={product.img[0]}
                        />
                    </Link>
                    <Link
                        onClick={onClose}
                        className="link"
                        to={`/productions/${product.id}`}
                    >
                        <Typography children={product.title} />
                    </Link>
                </Box>
                <Box>
                    <IconButton
                        onClick={(e) => setOpenMenu(e.currentTarget)}
                        children={<MoreVert />}
                    />
                </Box>
            </Box>
            <Box sx={styles.Box.Title}>
                <Counter
                    product={product}
                    list={BasketList}
                    setNumber={setBasketList}
                />

                <Typography
                    variant="h6"
                    children={`${product.prise * product.Quantity} ₴`}
                />
            </Box>
            <Divider sx={styles.Box.Divider} />

            <Menu
                anchorEl={OpenMenu}
                open={Boolean(OpenMenu)}
                onClose={() => setOpenMenu(null)}
                anchorOrigin={{
                    vertical: "top",
                    horizontal: "right",
                }}
                transformOrigin={{
                    vertical: "top",
                    horizontal: "right",
                }}
            >
                <MenuItem
                    onClick={() => {
                        BasketAdd(product);
                        setOpenMenu(null);
                    }}
                >
                    <ListItemIcon children={<Delete />} />
                    Видалити
                </MenuItem>
            </Menu>
        </Box>
    );
}

export default BasketItem;
